import React, {FC, ReactNode} from 'react';
import NextLink from 'next/link';

interface IProps {
  children: ReactNode;
  href: string;
  as?: string;
}

const Link: FC<IProps> = ({children, href, as}) => {
  return (
    <NextLink href={href} as={as}>
      <a>{children}</a>
    </NextLink>
  );
};

export default Link;
